<template>
  <div class="singleBlogItem">
    <headerItem></headerItem>
    <div class="content">
      <el-row :gutter="20">
        <el-col :span="8" :offset="4">
          <div class="content-left">
            <span class="title">{{jobInfo.jobName}}</span>

            <p>
              <span class="job-item job-pay">{{jobInfo.jobPay}}</span>
              <span class="job-item">{{jobInfo.degree}}</span>
              <span class="job-item">{{jobInfo.workExp}}</span>
            </p>
            <p>发布人:{{entInfo.entContectName}}</p>
            <p>更新时间:{{jobInfo.updateTime}}</p>

            <el-tag type="success" v-for="(tag,index) in jobInfo.entTag" :key="index">{{tag}}</el-tag>
          </div>
          <span>
            <b>职位描述</b>
          </span>
          <hr>
          <p class="job-req">{{jobInfo.jobReq}}</p>

          <span>
            <b>工作地点</b>
          </span>
          <hr>
          <p class="job-req">{{jobInfo.jobAddress}}</p>
        </el-col>

        <el-col :span="6" :offset="4">
          <div class="content-right">
            <el-card class="box-card">
              <div slot="header" class="clearfix">
                <span>{{entInfo.entName}}</span>
                <p>
                  <span>{{entInfo.entClass}}</span>|
                  <span>{{entInfo.entScale}}</span>
                </p>
              </div>
              <div>
                <p>{{entInfo.entSummary}}</p>
              </div>
            </el-card>
          </div>
        </el-col>
      </el-row>
    </div>

    <div class="comment">
      <el-row :gutter="20">
        <el-col :span="8" :offset="4">
          <el-button type="success" @click="funSendResume" v-if="!entid">发送简历</el-button>
          <el-button type="success" @click="funPass" v-else>接受邀请</el-button>
        </el-col>
      </el-row>
    </div>
    <footerItem></footerItem>
  </div>
</template>

<script>
import Cookies from "vue-cookies";
import headerItem from "./childPageHeader";
import footerItem from "./childPageFooter";
export default {
  components: {
    headerItem,
    footerItem
  },
  name: "singleBlogItem",
  data() {
    return {
      jobid: "",
      resumid: "",
      entid: "",
      jobInfo: {},
      entInfo: {}
    };
  },
  created() {
    this.jobid = this.$route.query.jobid;
    this.resumeid = this.$route.query.resumeid;
    this.entid = this.$route.query.entid;
    this.axios
      .post("searchJob", {
        key: this.jobid,
        type: "single"
      })
      .then(data => {
        if (data.data.status == "ok") {
          let jobObj = JSON.parse(data.data.msg.entJobInfo)[0].fields;
          jobObj.jobid = JSON.parse(data.data.msg.entJobInfo)[0].pk;
          jobObj.entTag = jobObj.entTag.split(",");
          this.jobInfo = jobObj;
          let entObj = JSON.parse(data.data.msg.entBaseInfo)[0].fields;
          this.entInfo = entObj;
        }
      });
  },
  methods: {
    funPass() {
      this.axios
        .post("passSent", {
          msg: {
            jobid: this.jobid,
            resumeid: this.resumeid,
            entid: this.entid,
            psnid: Cookies.get("psnid")
          }
        })
        .then(data => {
          if (data.data.status == "ok") {
            this.$message({
              type: "success",
              message: data.data.msg
            });
            window.close();
          }
        });
    },
    funSendResume() {
      this.axios
        .post("sendResume", {
          type: "psnsend",
          psnid: Cookies.get("psnid"),
          jobid: this.jobid
        })
        .then(data => {
          if (data.data.status == "ok") {
            this.$message({
              type: "success",
              message: data.data.msg
            });
          } else {
            this.$message({
              type: "error",
              message: data.data.msg
            });
          }
        });
    }
  }
};
</script>
<style scoped>
.content {
  padding: 60px;
  color: #303133;
}
.content-left {
  padding-bottom: 20px;
}
.content-left .title {
  font-size: 18px;
  color: #606266;
}
.content-left p {
  font-size: 14px;
  color: #909399;
}
.content-right .box-card {
  background: #f8f8f9;
}
article {
  padding: 10px;
}
.comment {
  padding: 60px;
  /* background: #67c23a; */
  height: 36px;
  color: #303133;
  font-size: 18px;
  line-height: 36px;
}

.el-tag {
  margin-right: 10px;
}
.job-req {
  margin: 20px 5px;
  padding: 20px 5px;
}

.content-left p .job-pay {
  color: #f56c6c;
}
.content-left p .job-item {
  margin-right: 5px;
}
</style>
